 .cd {
    display: inline-block;
    margin: 3vh;
    padding: 3vh;
    border: solid;
    border-radius: 15%;
    background-color: black;
    opacity: 0.9;
 }

 .counter {
    margin: 3vh;
    
 }

 .message {
    border: none;
    border-radius: 15%;
    background-color: black;
    display: inline-block;
    padding: 3vh;
    margin: 3vh;
    opacity: 0.9;
 }
</style>
